.row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.row > button {
  margin-left: 4px;
  margin-right: 8px;
}

.row:not(:last-child) {
  margin-bottom: 16px;
}

.button {
  appearance: none;
  background: none;
  font-size: 32px;
  padding-left: 12px;
  padding-right: 12px;
  outline: none;
  border: 2px solid transparent;
  color: #0057d7;
  padding-bottom: 4px;
  cursor: pointer;
  background-color: rgba(0, 87, 215, 0.1);
  border-radius: 2px;
  transition: all 0.15s;
}

.button:hover,
.button:focus {
  border: 2px solid rgba(0, 87, 215, 0.4);
}

.button:active {
  background-color: rgba(0, 87, 215, 0.2);
}

.popup {
  max-width: 1000px !important;
  min-width: 300px;
}
